import { useEffect } from 'react';
import { paramCase } from 'change-case';
import { useParams, useLocation, Link as RouterLink } from 'react-router-dom';
// material
import { Icon } from '@iconify/react';
import plusFill from '@iconify/icons-eva/plus-fill';
import { Container, Button, Stack } from '@mui/material';
// redux
import { useDispatch, useSelector } from '../../redux/store';
import { getTirageConfs, getAgentConfs, getPrimeConfs } from '../../redux/slices/user';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import PrimeConfNewForm from '../../components/_dashboard/tirageConf/PrimeConfNewForm';

// ----------------------------------------------------------------------

export default function TirageConfCreate() {
  const { themeStretch } = useSettings();
  const dispatch = useDispatch();
  const { pathname } = useLocation();
  const { type, configurationId } = useParams();
  const { tirageprimeConfs, agentprimeConfs, primeConfs } = useSelector((state) => state.user);
  const primeConfSp = type === 'tirage' ? tirageprimeConfs : agentprimeConfs;
  const currentConf = primeConfSp?.find((conf) => conf?.id === Number(configurationId));
  const isEdit = pathname.includes('edit');

  useEffect(() => {
    dispatch(getPrimeConfs());
    if (isEdit) {
      dispatch(getTirageConfs());
      dispatch(getAgentConfs());
    }
  }, [dispatch]);

  return (
    <Page title={`Configuration: ${isEdit ? 'Modifier' : 'Création'} prime par ${type} | RLP Center Lotto`}>
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading={!isEdit ? `Créer une configuration ${type}` : `Modifier prime pour ${currentConf?.name}`}
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            { name: 'Prime generale', href: PATH_DASHBOARD.tirage?.configuration },
            { name: !isEdit ? `Nouvel Configuration ${type}` : currentConf?.name }
          ]}
        />

        {primeConfs && <PrimeConfNewForm isEdit={isEdit} currentConf={isEdit ? currentConf : primeConfs} type={type} />}
      </Container>
    </Page>
  );
}
